.contactFeel {
  background: linear-gradient(to right, #8722a9, #288ea2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: #8722a9 !important;
  float: left;
  margin-left: 200px;
}

.footer {
  display: flex;
  flex-direction: row;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  align-items: center;
  align-content: center;
  justify-content: space-between;
}

.fa-icons {
  font-size: 24px;
}

.App-Social-Footer {
  float: right;
  margin-right: 200px;
}

.fa-icons {
  font-size: 24px;
  margin: 15px;
}

h5 {
  color: white;
}

@media (max-width: 768px) {
  /* Mobile adjustments */
  .telegram {
      display: none;
  }

  .App-Social-Footer {
      display: none;
  }

  /* Align .contactFeel and .email left and right in mobile */
  .contactFeel {
      float: left;
      margin-left: 20px; /* Adjust left margin */
      text-align: left;  /* Ensure left alignment */
  }

  .email {
      float: right;
      margin-right: 20px; /* Adjust right margin */
      text-align: right;  /* Ensure right alignment */
  }
}
