.App {
  text-align: center;
}

.App-Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 200px;
  background-color: #0F1724;
  width: 100%;
  height: 100px;
  position: sticky;
  top: 0;
  z-index: 1;
}

.App-Navigation {
  display: flex;
  gap: 20px;
  justify-content: center;
}
.App-Navigation a {
  font-weight: 500;
  font-size: 20px;
  margin-left: 60px;
  margin-right: 60px;
}
.App-Navigation .App-Header-NavLink {
  margin: 0 30px;
}
.App-Navigation .App-Header-NavLink a {
  color: #fff;
  text-decoration: none;
}

.App-Title {
  color: white;
  font-weight: 700;
  font-size: 1.5em;
}

.fa-icons {
  font-size: 24px;
  margin: 15px;
}
.close-icon {
  display: none;
}
.hamburger-icon {
  display: none;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

@media (max-width: 768px) {
  /* Animação do Sidebar */
  @keyframes slideIn {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes slideOut {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }

  .App-Navigation a {
    font-weight: 500;
    font-size: 20px;
    margin-left: 0px;
    margin-right: 0px;
  }

  .App-Navigation {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    height: 100%;
    background-color: #09111e;
    z-index: 2;
    padding: 30px;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
  }

  .App-Navigation.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    animation-name: slideIn;
  }

  .App-Navigation:not(.active) {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
    animation-name: slideOut; /* Aplica animação de saída */
    animation-duration: 0.3s; /* Ajusta a duração da animação de saída */
    animation-timing-function: ease-in-out; /* Usando o timing para suavizar a animação de fechamento */
  }

  .App-Sidebar-Footer {
    position: absolute;
    bottom: 0;
    width: 91%;
    background-color: #09111e;
    padding: 20px;
    text-align: center;
  }

  .copyright {
    color: white;
    font-size: 14px;
  }

  .App-Navigation .App-Header-NavLink {
    margin: 0;
  }

  .App-Navigation .App-Header-NavLink a {
    color: #fff;
    text-decoration: none;
  }

  /* Ícones das Redes Sociais */
  .App-Social {
    margin-top: 20px;
    display: flex;
    flex-direction: row; /* Garante que os ícones fiquem lado a lado */
    justify-content: baseline;
    margin: 0px;
    gap: 10px; /* Ajuste o espaçamento entre os ícones */
    flex-wrap: nowrap; /* Impede que os ícones se empilhem */
  }

  .App-Social a {
    text-decoration: none;
    color: white;
    font-size: 24px;
    transition: color 0.3s ease;
  }

  .App-Social a:hover {
    color: #8722a9; /* Cor ao passar o mouse */
  }

  .hamburger-icon {
    display: block;
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 30px;
    cursor: pointer;
    color: white;
    z-index: 2;
  }

  .close-icon {
    display: block;
    align-self: flex-end;
    font-size: 25px;
    margin-bottom: 20px;
    cursor: pointer;
    color: white;
  }
}


/* No Desktop (sem a sidebar) */
@media (min-width: 769px) {
  .hamburger-icon {
    display: none; /* Esconde o ícone de hambúrguer no desktop */
  }

  .App-Navigation {
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
  }

  .App-Social {
    display: flex;
    justify-content: center;
    gap: 15px;
  }

  .App-Social a {
    color: white;
    font-size: 24px;
    text-decoration: none;
  }
}
