// Variables
$primary-color: #8722a9;
$secondary-color: #288ea2;
$background-color: #0f1724;
$logo-size: 400px;
$mobile-logo-size: 200px;

.App-Intro {
    font-size: 5vh;
    color: white;
}

.Intro1 {
    background: linear-gradient(to right, #8722a9, #288ea2); /* Standard syntax */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text; /* Standard syntax */
    color: #8722a9;      /* Fallback text color if gradient clip fails */ /* same color as the first color of the gradient */
}

.App-landingText {
    margin-top: 200px;
    padding: 0 200px;
    float: left;
    display: flex;
    flex-direction: column;
    text-align: center;
    .App-Logo {
        margin-bottom: 10px;
    }
}

.App-bio {
    color: #727c94;
}

.App-CVDown {
    background: linear-gradient(to right, #8722a9, #288ea2); /* Standard syntax */
    padding: 10px;
    border-radius: 25px;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 18px;
}
a {
    margin-top: 20px;
    text-decoration: none;
    color: white;
}

.App-Logo {
    border-radius: 50%;
    margin: 115px 0;
}

/* Media Queries para Mobile */
@media (max-width: 768px) {
    .App {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $background-color;

    }
    
    .App-Logo {
        width: $logo-size;
        height: $logo-size;
        border-radius: 50%; // Make logo round
        background-color: $background-color;
        margin-bottom: 20px;
        margin-top: 0;
    }
    
    .App-Intro {
        font-size: 5vh;
        color: white;
        margin: 10px 0; /* Reduzido para ajuste no mobile */
    }
    
    .Intro1 {
        background: linear-gradient(to right, $primary-color, $secondary-color); /* Standard syntax */
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text; /* Standard syntax */
        color: $primary-color; /* Fallback text color */
    }
    
    .App-landingText {
        padding: 0 20px; /* Reduzido para ajuste no mobile */
        margin-top: 0px;
        display: flex;
        flex-direction: column;
        align-items: center; /* Centraliza os itens */
    }
    
    .App-bio {
        color: #727c94;
        margin-bottom: 20px;
    }
    
    .App-CVDown {
        background: linear-gradient(to right, $primary-color, $secondary-color); /* Standard syntax */
        padding: 10px 20px; /* Ajustado para ajuste no mobile */
        border-radius: 25px;
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        font-size: 18px;
        text-decoration: none;
        color: white;
        margin-top: 20px;
        display: inline-block; /* Garante espaçamento consistente */
    }
    .App {
        padding: 20px; /* Adiciona um padding para evitar bordas apertadas */
    }

    .App-Logo {
        width: $mobile-logo-size;
        height: $mobile-logo-size;
        margin-bottom: 15px; /* Reduzido para ajuste no mobile */
    }

    .App-Intro {
        font-size: 3.5vh; /* Texto menor no mobile */
    }

    .App-bio {
        font-size: 14px; /* Texto menor no mobile */
    }

    .App-CVDown {
        font-size: 16px; /* Botão menor no mobile */
        padding: 8px 16px; /* Ajuste de espaçamento */
    }
}
